@import "../../global.scss";

.navbar {
  background-color: $navbarColor;
  color: $textColor;
  position: fixed;
  top: 0;
  width: 100%;
  height: $navbarHeight;
  display: flex;
  box-shadow: 0px 5px 10px 2px rgba(0, 0, 0, 0.2);
  z-index: 4;

  .relative {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    width: 100%;

    .left {
      padding-left: 15px;
      display: flex;
      justify-content: center;
      align-items: center;

      .hamburger {
        display: none;

        @include mobile {
          display: block;
        }
      }
    }
    .middle {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);

      .link-title {
        text-decoration: none;

        h2 {
          color: $textColor;
        }
      }
    }
    .right {
      padding-right: 15px;
      display: flex;
      align-items: center;

      button {
        background-color: transparent;
        color: $textColor;
        border-radius: 3px;
        border: none;
        outline: none;
        font-size: 16px;
        font-weight: 700;
        padding: 5px 10px;
        cursor: pointer;
        margin-right: 5px;

        &:hover {
          background-color: rgba(255, 255, 255, 0.247);
        }
      }
    }
    .icon {
      font-size: 32px;
      cursor: pointer;
    }
  }
}
